@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700&family=Montserrat:wght@100;200;300;400;500;600;700;800&family=Poppins:wght@100;200;300;400;500;600;700;800&display=swap");
:root {
  --font-family: "Poppins", sans-serif;
  --font-h1: 3.5rem;
  --font-h2: 3rem;
  --font-h3: 2.5rem;
  --font-p: 1.25rem;
  --font-link: 1.35rem;
  /* COLORS */
  --bs-blue: #0d6efd;
  --bs-indigo: #6610f2;
  --bs-purple: #6f42c1;
  --bs-purple-100: #863eb4;
  --bs-pink: #d63384;
  --bs-red: #dc3545;
  --bs-white: #fff;
  --bs-gray-dark: #3e4265;
  --bs-gray-500: #b4b7c9;
  --bs-gray-600: #9397ad;
  --bs-gray-700: #585c7b;
  --bs-gray-800: #3e4265;
  --bs-gray-900: #131022;
  --bs-primary: #6366f1;
  --bs-secondary: #eff2fc;
  --bs-success: #22c55e;
  --bs-info: #4c82f7;
  --bs-warning: #ffba08;
  --bs-danger: #ef4444;
  --bs-dark: #131022;
  /* COLOR SOURCE */
  --nav-link: var(--bs-gray-800);
  --nav-link-hover: var(--bs-purple);
  --nav-icons: var(--bs-purple);
  --text-color: var(--bs-white);
  --background-header: var(--bs-white);
  --background: var( --bs-dark);
  --background-light: #ffffff0d;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  scroll-behavior: smooth;
}

body {
  font-family: var(--font-family);
  background-color: var(--background);
}

li {
  list-style: none;
}

a {
  text-decoration: none;
  font-size: var(--font-link);
}

h1,
h2,
h3,
p {
  color: var(--text-color);
}

h1 {
  font-size: var(--font-h1);
}

h2 {
  font-size: var(--font-h2);
}

h3 {
  font-size: var(--font-h3);
}

p {
  font-size: var(--font-p);
}

img {
  width: 100%;
  height: auto;
}

.data {
  background-color: var(--background-light);
  border-radius: 5px;
  margin: 0 auto;
}

.header {
  background-color: var(--background-header);
  box-shadow: 0 1px 1px 2px #6f42c1;
}
.header__menu {
  display: none;
  position: fixed;
  top: 2rem;
  right: 1rem;
  z-index: 5;
}
.header__menu-icon {
  align-self: center;
  color: var(--nav-icons);
  cursor: pointer;
  font-size: 3rem;
}

.nav {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.nav__logo h1 {
  font-size: 2.5rem;
  color: var(--nav-icons);
  transition: all 0.2s ease;
}
.nav__logo h1:hover {
  color: var(--nav-link);
}
.nav__list {
  display: flex;
}
.nav__item:not(:last-child) {
  margin-right: 2rem;
}
.nav__link {
  color: var(--nav-link);
  letter-spacing: 0.025rem;
  font-size: 1.75rem;
  font-weight: 400;
  transition: all 0.2s ease;
}
.nav__link:hover {
  color: var(--nav-link-hover);
  font-weight: 400;
}
.nav__icons {
  color: #211a2d;
}
.nav__icons-icon {
  fill: var(--nav-icons);
  cursor: pointer;
  width: 2.5rem;
  transition: all 0.2s ease;
}
.nav__icons-icon:hover {
  fill: var(--nav-link);
}

@media screen and (max-width: 500px) {
  .header__menu {
    display: block;
  }

  .nav__icons {
    display: none;
  }

  .nav__list {
    display: none;
  }
}
footer {
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 1px 1px 5px #6f42c1;
  background-color: var(--bs-white);
}
footer p {
  color: var(--bs-gray-800);
  font-size: 1.225rem;
  font-weight: 400;
  letter-spacing: 0.105rem;
}
footer .arrow-up {
  position: absolute;
  cursor: pointer;
  right: 2rem;
  z-index: 10;
  fill: var(--bs-purple);
  width: 3.5rem;
  height: 3.5rem;
}

header {
  display: flex;
  align-items: center;
  height: 7rem;
  padding: 0 2rem;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 15;
}

main {
  width: 100%;
  height: auto;
}

section {
  padding-top: 7rem;
  width: 100%;
  height: 100vh;
}

.container {
  margin: 0 auto;
  max-width: 1144px;
  height: 91vh;
  padding: 5rem 0;
}
@media screen and (max-width: 1144px) {
  .container {
    padding: 5rem 2rem;
  }
}

footer {
  width: 100%;
  height: 15vh;
}

.bg-grid {
  display: grid;
}

.active {
  color: var(--nav-link-hover);
}

.container {
  text-align: center;
  align-items: center;
  padding-top: 5rem;
}
.container .contact__form {
  width: 40rem;
  align-self: center;
  justify-self: center;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  padding-bottom: 5rem;
}
.container .contact__input, .container .contact__textarea, .container .contact__button {
  width: inherit;
  border-radius: 0.5rem;
  padding: 1rem;
  outline: none;
  font-size: 1.5rem;
}
.container .contact__input, .container .contact__textarea {
  border: 2px solid #fff;
  color: var(--bs-gray-900);
}
.container .contact__input:focus, .container .contact__textarea:focus {
  border: 2px solid var(--bs-indigo);
  color: var(--bs-indigo);
}
.container .contact__input:focus::placeholder, .container .contact__textarea:focus::placeholder {
  color: var(--bs-indigo);
}
.container .contact__input::placeholder, .container .contact__textarea::placeholder {
  color: var(--bs-gray-900);
}
.container .contact__textarea {
  font-family: var(--font-family);
  height: 15rem;
}
.container .contact__button {
  border: none;
  height: 4rem;
  width: auto;
  background-color: var(--bs-indigo);
  color: var(--bs-white);
  transition: 0.2s ease;
  border-radius: 0.5rem;
  font-size: 1.5rem;
  cursor: pointer;
}
.container .contact__button:hover {
  background-color: var(--bs-purple);
}

.about .container .data {
  display: flex;
  flex-direction: column;
  gap: 3rem;
  padding: 11rem 5rem;
  max-width: 500rem;
  grid-template-columns: 100%;
}
.about .container .data h1,
.about .container .data p,
.about .container .data button {
  align-self: center;
  text-align: center;
}
.about .container .data h1 {
  font-size: 4rem;
  font-weight: 600;
}
.about .container .data p {
  font-size: 1.75rem;
}
.about .container .data__button {
  border: none;
  height: 4rem;
  width: 14rem;
  background-color: var(--bs-indigo);
  color: var(--bs-white);
  transition: 0.2s ease;
  border-radius: 0.5rem;
  font-size: 1.5rem;
  cursor: pointer;
}
.about .container .data__button:hover {
  background-color: var(--bs-purple);
}
.about .container .data h1 {
  margin-bottom: 2rem;
}

.home .container {
  display: grid;
  grid-template-columns: 100%;
  padding-bottom: 15rem;
  align-items: center;
  justify-content: center;
  max-width: 100vw;
  background-image: linear-gradient(180deg, rgba(19, 16, 34, 0.88) 100%, rgba(62, 66, 101, 0.8) 10%, transparent 50%), url(../../assets/img/pexels-startup-stock-photos-7096.jpg);
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: cover;
  box-shadow: 0 1.5rem 4rem rgba(19, 16, 34, 0.2);
}
.home .container .data-home {
  background-color: transparent;
  border-radius: 5px;
  margin: 0 auto;
}
.home .container .data-home h1 {
  font-size: 4.5rem;
}
.home .container .data-home-title {
  color: var(--bs-purple);
}
.home .container .data-home-description {
  color: var(--bs-gray-500);
  font-size: 1.5rem;
  margin-top: 1rem;
}
.home .container .data-home button {
  margin-top: 3rem;
}
.home .container .data-home__button {
  border: none;
  height: 4rem;
  width: 13rem;
  background-color: var(--bs-indigo);
  color: var(--bs-white);
  transition: 0.2s ease;
  border-radius: 0.5rem;
  font-size: 1.5rem;
  cursor: pointer;
}
.home .container .data-home__button:hover {
  background-color: var(--bs-purple);
}

@media screen and (max-width: 1100px) {
  .home .container {
    height: 100%;
    background-size: 130%;
  }
  .home .container .data-home h1 {
    font-size: 4.2rem;
  }
  .home .container .data-home-description {
    font-size: 1.4rem;
    margin-top: 1rem;
  }
}
@media screen and (max-width: 840px) {
  .home .container {
    height: 100%;
    background-size: 200%;
    padding-bottom: 15.5rem;
  }
  .home .container .data-home h1 {
    font-size: 4rem;
  }
  .home .container .data-home-description {
    font-size: 1.3rem;
    margin-top: 1rem;
  }
}
@media screen and (max-width: 720px) {
  .home .container {
    height: 100%;
    background-size: 220%;
    padding-bottom: 15.9rem;
  }
  .home .container .data-home h1 {
    font-size: 3rem;
  }
  .home .container .data-home-description {
    font-size: 1.19rem;
  }
  .home .container .data-home button {
    margin-top: 3.5rem;
  }
  .home .container .data-home__button {
    border: none;
    height: 4rem;
    width: 12rem;
    background-color: var(--bs-indigo);
    color: var(--bs-white);
    transition: 0.2s ease;
    border-radius: 0.5rem;
    font-size: 1.35rem;
    cursor: pointer;
  }
  .home .container .data-home__button:hover {
    background-color: var(--bs-purple);
  }
}